<template>
  <v-container fill-height class="d-flex justify-center flex-column align-start" @click="change">
    <span style="font-family: 'simfang';" class="text_xxxlarge d-flex welcome_title mt-n16">
      <span class="text_thick mt-n2 text_xxxxlarge red--text">火</span>
      <span>锅底料</span>
    </span>
    <span class="ml-5 grey--text">{{day}}</span>
    <div style="width:100%;height:300px">
      <v-img
        :src="require('../../static/images/封面/2.png')"
        width="240px"
        height="240px"
        contain
        class="mx-auto mt-3"
      ></v-img>
    </div>

    <span class="mx-auto text_thick hitokoto d-flex justify-center">
      <span>『{{motto.hitokoto}}』</span>
    </span>
    <span class="mx-auto">———《{{motto.from}}》</span>
  </v-container>
</template>

<script>
import axios from "axios";
import qs from "qs";
function getMotto() {
  return new Promise((resolve, reject) => {
    axios({
      method: "get",
      url:
        "https://v1.hitokoto.cn/?c=h&c=d&c=i&c=a&encode=json&min_length=6&max_length=50", // 接口地址
    }).then((res) => {
      resolve(res.data); // 成功的返回
    });
  });
}
function CNDateString(date) {
  var cn = [" 〇 ", "一", "二", "三", "四", "五", "六", "七", "八", "九"];
  var s = [];
  // var YY = date.getFullYear().toString();
  // for (var i = 0; i < YY.length; i++)
  //   if (cn[YY.charAt(i)]) s.push(cn[YY.charAt(i)]);
  //   else s.push(YY.charAt(i));
  // s.push("年");
  var MM = date.getMonth() + 1;
  if (MM < 10) s.push(cn[MM]);
  else if (MM < 20) s.push("十" + cn[MM % 10]);
  s.push("月");
  var DD = date.getDate();
  if (DD < 10) s.push(cn[DD]);
  else if (DD < 20) s.push("十" + cn[DD % 10]);
  else s.push("二十" + cn[DD % 10]);
  s.push("日");
  return s.join("");
}
export default {
  name: "welcome",
  data() {
    return {
      motto: {
        hitokoto: "",
        from: "",
        day: "",
      },
    };
  },
  async created() {
    this.day = CNDateString(new Date())
    var that = this;
    document.addEventListener("deviceready", function () {
      cordova.getAppVersion.getVersionNumber().then(function (version) {
        //获取程序配置信息
        that.$request('get', '/user/getAppConfig', {}, {})
          .then(res => {
            sessionStorage.setItem('remoteVersion', res.data.version);
            sessionStorage.setItem('o', res.data.o - new Date().getTime());
            sessionStorage.setItem('hotExplainContent', res.data.hotExplainContent);
            sessionStorage.setItem('processExplainContent', res.data.processExplainContent);
            sessionStorage.setItem('shieldExplainContent', res.data.shieldExplainContent);
            sessionStorage.setItem('shieldCommentExContent',res.data.shieldCommentExContent)
            if (res.data.version == version) { sessionStorage.setItem('version', version); }
            else {
              that.showSnackbar('您的软件不是最新版,请前往下载最新版app');
              setTimeout(() => {
                window.open('http://scuhuoguodiliao.com', '_system', {})
              }, 1200);
            }
          })
          .catch(e => {
            that.showSnackbar(e)
            setTimeout(() => {
              navigator.app.exitApp();
            }, 1000);
          })

      })
    })
    setTimeout(() => {
      if (sessionStorage.getItem('remoteVersion') && sessionStorage.getItem('remoteVersion') != sessionStorage.getItem('version')) {
        that.showSnackbar('您的软件不是最新版,请前往下载最新版app');
        setTimeout(() => {
          window.open('http://scuhuoguodiliao.com', '_system', {})
        }, 1200);
      }
      else if (!sessionStorage.getItem('version')) {
        this.showSnackbar('app还未加载完成')
      }
      else if (localStorage.getItem("token") && this.$route.path == "/") {
        that.$request('post', '/user/login', {}, {
          token: localStorage.getItem("token")
        })
          .then(async (res) => {
            that.$router.replace("/main/index")
          })
          .catch(e => {
            that.$router.replace("/login")
          })
      } else if (this.$route.path == "/") {
        that.$router.replace("/login");
      }
    }, 6000);
    let res = await getMotto();
    this.motto.hitokoto = res.hitokoto;
    this.motto.from = res.from;
    console.log(this.motto);
  },
  methods: {

    change() {
      let that = this;
      if (sessionStorage.getItem('remoteVersion') && sessionStorage.getItem('remoteVersion') != sessionStorage.getItem('version')) {
        that.showSnackbar('您的软件不是最新版,请前往下载最新版app');
        setTimeout(() => {
          window.open('http://scuhuoguodiliao.com', '_system', {})
        }, 1200);
      }
      else if (!sessionStorage.getItem('version') || !sessionStorage.getItem('remoteVersion')) {
        this.showSnackbar('app还未加载完成')
      }
      else if (localStorage.getItem("token")) {
        that.$request('post', '/user/login', {}, {
          token: localStorage.getItem("token")
        })
          .then(async (res) => {
            that.$router.replace("/main/index")
          })
          .catch(e => {
            that.$router.replace("/login")
          })
      } else this.$router.replace("/login");
    },
  },
};
</script>

<style>
.welcome_title {
  margin-left: 20px;
}
.hitokoto {
  width: 80%;
}
</style>



